* {
  box-sizing: border-box;
}

:root {
  --background-color: #d1eae8;
  --text-color: #2e2f42;
  --button-background-color: #f4f4fd;
  --button-focus-color: rgba(18, 156, 131, .776);
  --button-text-focus: #fafafa;
  --card-background: #b5c8c5;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: var(--background-color);
  color: var(--text-color);
  font-family: Roboto, sans-serif;
  line-height: 1.5;
}

p {
  margin-top: 0;
  margin-bottom: 0;
}

img {
  max-width: 100%;
  border: 1px solid #0d161b;
  display: block;
}

.is-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.search-form {
  width: 100%;
  background-image: linear-gradient(to top, #fafafa, #15b7b2);
  justify-content: center;
  gap: 30px;
  padding: 15px 0;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

input {
  width: 300px;
  height: 40px;
  border: none;
  border-radius: 5px;
  outline: none;
  padding: 5px 5px 5px 15px;
  font-size: 16px;
}

button {
  width: 100px;
  height: 40px;
  color: var(--text-color);
  cursor: pointer;
  background-color: var(--button-background-color);
  border: none;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  font-size: 16px;
  font-weight: 600;
  display: flex;
}

button:hover, button:focus {
  color: var(--button-text-focus);
  background-color: var(--button-focus-color);
  border: 1px solid #fafafa;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  gap: 10px;
  margin-top: 70px;
  display: flex;
}

.photo-card {
  width: 280px;
  background-color: var(--card-background);
  border-radius: 5px;
  padding: 0;
}

.img_wrap {
  height: auto;
  align-items: center;
  display: flex;
}

.gallery_link {
  text-decoration: none;
  display: block;
}

.info {
  flex-direction: column;
  align-items: center;
  gap: 5px;
  margin-top: 5px;
  margin-bottom: 5px;
  font-size: 12px;
  display: flex;
}

